fter{\r\n\tleft: 2px;\r\n}\r\n.about-logo{\r\n\tmargin-left:22%;\r\n}\r\n.about-name{\r\n\tfont-size:15px;\r\n\tfont-weight:bold;\r\n}\r\n.undo_button {\r\n\tdisplay: none;\r\n\twidth: 50px;\r\n\theight: 50px;\r\n\ttop: 0;\r\n\tborder: 0;\r\n\toutline: none;\r\n\tcursor: pointer;\r\n\tfilter: var(--mobile-menu-toggle-filter);\r\n\tbackground: url(images/icons/undo.svg) no-repeat center center;\r\n\tbackground-size: auto 25px;\r\n\tmargin-left: 10px;\r\n}\r\n.undo_button:hover {\r\n\tbackground-color: transparent;\r\n}\r\n@media screen and (max-width: 700px){\r\n\t.undo_button {\r\n\t\tdisplay: block;\r\n\t}\r\n}\r\n\r\n/* ========== sub-header ==================================================== */\r\n\r\n.submenu{\r\n\t-ms-grid-row: 1;\r\n\t-ms-grid-column: 1;\r\n\t-ms-grid-column-span: 3;\r\n\tgrid-area: submenu;\r\n\tdisplay: flex;\r\n\tflex-direction: row;\r\n\talign-items: center;\r\n\tbackground-color: rgba(255, 255, 255, 0.2);\r\n\tbackground-color: var(--section-background-color);\r\n\toverflow: hidden;\r\n\tmargin-bottom: 5px;\r\n}\r\n.attributes{\r\n\tdisplay: flex;\r\n\tflex-wrap: nowrap;\r\n\tbackground-color: var(--area-background-color);\r\n\twidth: calc(100% - 125px);\r\n\tmargin-top: 5px;\r\n\tmargin-bottom: 5px !important;\r\n\tpadding: 3px 10px 3px 10px;\r\n\tborder: 0;\r\n\toverflow-x: auto;\r\n\toverflow-y: hidden;\r\n\twhite-space: nowrap;\r\n\tmin-height: 30px;\r\n}\r\n.attributes .item{\r\n\tdisplay: inline-flex;\r\n\talign-items: center;\r\n\tmargin-right: 20px;\r\n}\r\n.attributes .item > label {\r\n\tmargin: 0 .5rem 0 0;\r\n}\r\n.attributes input[type=\"number\"]{\r\n\twidth: 60px;\r\n\tmargin-right: 5px;\r\n}\r\n.attributes input[type=\"color\"] {\r\n\tcursor: pointer;\r\n\tpadding: 0;\r\n\tborder: .2rem solid var(--input-background-color);\r\n\twidth: 3rem;\r\n}\r\n.attributes .item > button:not(.ui_icon_button){\r\n\tdisplay: inline-block;\r\n\tpadding: 3px 10px;\r\n}\r\n\r\n/* ========== left sidebar ================================================== */\r\n\r\n.sidebar_left{\r\n\t-ms-grid-row: 2;\r\n\t-ms-grid-column: 1;\r\n\tgrid-area: sidebar_left;\r\n\tdisplay: flex;\r\n\tflex-direction: row;\r\n\tflex-wrap: wrap;\r\n\tbackground-color: var(--section-background-color);\r\n\tpadding: 0 5px 5px 0;\r\n\tmargin-right: 5px;\r\n\toverflow: hidden;\r\n\talign-self: start;\r\n\twidth: 40px;\r\n\toverflow-y: auto;\r\n\tmax-height: 100%;\r\n}\r\n.sidebar_left .item{\r\n\tposition: relative;\r\n\tdisplay:block;\r\n\tbackground-color: var(--area-background-color);\r\n\theight: 25px;\r\n\twidth: 30px;\r\n\tmargin: 5px 0 0 5px;\r\n\toverflow: hidden;\r\n\tcursor: pointer;\r\n}\r\n.sidebar_left .item:after{\r\n\tposition: absolute;\r\n\tcontent: '';\r\n\tleft:0;\r\n\ttop:0;\r\n\tbottom:0;\r\n\tright:0;\r\n\tfilter: var(--menu-icons-filter);\r\n\tbackground-position: center center;\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-size: 20px 20px;\r\n}\r\n.sidebar_left .item:hover{\r\n\tbackground-color: var(--background-color-hover);\r\n}\r\n.sidebar_left .item.active{\r\n\tbackground-color: var(--background-color-active);\r\n\tcolor: var(--text-color-active);\r\n}\r\n.sidebar_left .item.active:after{\r\n\tfilter: var(--menu-icons-filter-active);\r\n}\r\n\r\n/*\r\nIMPORTANT: any new icon should also must be added on /service-worker.js + its version should be updated - FEATURE DISABLED\r\n */\r\n.sidebar_left .pan:after{ background-image: url('images/icons/pan.svg'); }\r\n.sidebar_left .select:after{ background-image: url('images/icons/select.svg'); }\r\n.sidebar_left .selection:after{ background-image: url('images/icons/selection.svg'); }\r\n.sidebar_left .brush:after{ background-image: url('images/icons/brush.svg'); }\r\n.sidebar_left .pencil:after{ background-image: url('images/icons/pencil.svg'); }\r\n.sidebar_left .pick_color:after{ background-image: url('images/icons/pick_color.svg'); }\r\n.sidebar_left .erase:after{ background-image: url('images/icons/erase.svg'); }\r\n.sidebar_left .magic_erase:after{